.list {
    margin-top: 20px;
    display: grid;
    gap: 15px;
}

.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.par {
    color: rgba(51, 51, 51, .8);
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.14;
    letter-spacing: .02em;

    @media screen and (min-width: 480px) {
        font-size: 18px;
    }

    @media screen and (min-width: 1240px) {
        font-size: 22px;
    }
}

.button {
    display: block;
    padding: 5px;
    background-color: rgb(245, 231, 204);
    color: rgba(51, 51, 51, .8);
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.14;
    letter-spacing: .02em;
    border: 1px solid rgba(51, 51, 51, .8);
    border-radius: 5px;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        transform: scale(1.05);
    }
}